@keyframes scaleUp {
	0% {
		transform: translate(-50%, -50%) scale(0.75);
		opacity: 0;
	}
	100% {
		transform: translate(-50%, -50%) scale(1);
		opacity: 1;
	}
}


@keyframes scaleDown {
	0% {
		transform: translate(-50%, -50%) scale(1);
		opacity: 1;
	}
	100% {
		transform: translate(-50%, -50%) scale(0.75);
		opacity: 0;
	}
}

@keyframes scaleButtonCloseUp {
	0% {
		transform: scale(0.75);
		opacity: 0;
	}
	80% {
		transform: scale(1.06);
		opacity: 1;
	}
	90% {
		transform: scale(0.9);
		opacity: 1;
	}
	95% {
		transform: scale(1.02);
		opacity: 1;
	}
	100% {
		transform: scale(1);
		opacity: 1;
	}
}

@keyframes scaleButtonCloseDown {
	0% {
		transform: scale(1);
		opacity: 1;
	}
	100% {
		transform: scale(0.75);
		opacity: 0;
	}
}

@keyframes opacityUp {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes opacityDown {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}